@import url("https://fonts.googleapis.com/css2?family=Karla:wght@200;300;500&display=swap");

* {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  max-width: 100vw;
  min-height: 100vh;
  background-color: #030326;
  color: white;
}

.white {
  color: s;
}

.center {
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cc {
  text-align: center;
}

.wide {
  padding: 10px;
  width: 350px;
  padding-bottom: 0px;
  border: transparent;
  font-weight: lighter;
  font-size: large;
}

.bra {
  border-radius: 10px;
}

.light {
  font-weight: lighter;
  letter-spacing: 2px;
  border-color: transparent;
}

.word_wrap {
  word-wrap: break-word;
  white-space: break-spaces;
  overflow-x: hidden;
}

.bccg::after {
  background-color: whitesmoke;
  border-radius: 2px;
}

.cw {
  color: white;
}
